<template>
  <div >
    <div style="display: flex;padding-left: 15%;height: 75px;">
      <div >
        <img :src="logo" class="loginLogoStyle" alt="logo" style="display: none"/>
      </div>
      <div class="appNames" style="height:75px;line-height: 75px;color: black;font-size: 20px;margin-left: 1%; display: none">
       {{appNames}}   <!--创蓝教育实训云平台-->
      </div>
    </div>
    <div class="center">
      <div class="xixi" style="display: table; margin-top: calc(50vh - 225px);">
        <el-form :model="loginForm" :rules="fieldRules" ref="loginForm" label-position="left" @load="refreshCaptcha"
                 label-width="0px" class="demo-ruleForm login-container">
          <h2 class="title" style="padding-left: 22px" >系统登录</h2>
          <el-tabs v-model="activeName">
            <el-tab-pane label="用户密码" name="user" >
              <div class="userPass">
                <el-form-item prop="account">
                  <el-input type="text" style="margin-top: 10px" v-model="loginForm.account" ref="account"
                            auto-complete="off" placeholder="请输入账号">
                    <i slot="prefix" class="el-icon-s-custom" style="margin-left: 5px;"></i>
                  </el-input>
                </el-form-item>
                <el-form-item prop="password" @keyup.enter.native="login">
                  <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder="请输入密码">
                    <i slot="prefix" class="el-icon-lock" style="margin-left: 5px"></i>
                  </el-input>
                </el-form-item>
                <el-form-item >
                  <el-col :span="12">
                    <el-form-item prop="captcha">
                      <el-input type="test" v-model="loginForm.captcha" auto-complete="off" placeholder="单击图片刷新"
                                style="width: 100%;"  @keyup.enter.native="login">
                        <i slot="prefix" class="el-icon-picture-outline" style="margin-left: 5px"></i>
                      </el-input>
                    </el-form-item>
                  </el-col>
                  <el-col class="line" :span="1">&nbsp;</el-col>
                  <el-col :span="11">
                    <el-form-item>
                      <img style="width: 100%;" class="pointer" :src="loginForm.src" @click="refreshCaptcha">
                    </el-form-item>
                  </el-col>
                </el-form-item>
                <el-form-item style="width:100%; height: 35px;margin-top: -30px; text-align: center">
                  <el-checkbox class="remember" >记住账号</el-checkbox>
                </el-form-item>
                <el-form-item style="width:100%;">
                  <el-button type="primary" style="width:48%;" @click.native.prevent="reset">重 置</el-button>
                  <el-button type="primary" style="width:48%;"
                             @click.native.prevent="login" :loading="loading">登 录</el-button>
                </el-form-item>
              </div>
            </el-tab-pane>
            <el-tab-pane label="短信验证码" name="code">
              <div class="phonePass">
                <el-form-item prop="phone">
                  <el-input type="text" style="margin-top: 20px" v-model="phoneForm.phone" auto-complete="off" placeholder="请输入手机号">
                    <i slot="prefix" class="fa fa-phone" style="margin-left: 5px;"></i>
                  </el-input>
                </el-form-item>
                <el-form-item>
                  <el-col :span="12">
                    <el-form-item prop="captcha">
                      <el-input type="test" v-model="loginForm.captcha" auto-complete="off" placeholder="单击发送短信"
                                style="width: 100%;margin-top: 20px">
                        <i slot="prefix" class="fa fa-commenting-o" style="margin-left: 5px;"></i>
                      </el-input>
                    </el-form-item>
                  </el-col>
                  <el-col class="line" :span="1">&nbsp;</el-col>
                  <el-col :span="11">
                    <el-form-item>
                      <el-button  style="width: 100%;margin-top: 20px;background: #b9b9b9;height: 40px">点击发送短信</el-button>
                    </el-form-item>
                  </el-col>
                </el-form-item>
                <el-form-item style="width:100%;">
                  <el-button type="primary" style="width:90%;margin-top: 20px"
                             @click.native.prevent="login" :loading="loading">登 录</el-button>
                </el-form-item>
              </div>
            </el-tab-pane>
          </el-tabs>
        </el-form>
      </div>
    </div>
    <div class="footer" style="display: none">
      <p style="margin-top:0">{{info}}</p>
      <p>联系电话：<a>{{phoneCom}}</a></p>
    </div>
  </div>
</template>

<script>

  import { mapState } from 'vuex'
  import {baseUrl} from "../utils/global";
  import Cookies from "js-cookie"
  import ThemePicker from "@/components/ThemePicker"
  import LangSelector from "@/components/LangSelector"
  export default {
    name: 'Login',
    components:{
      ThemePicker,
      LangSelector
    },
    data() {
      return {
        phoneCom:'0731-89913439',
        info:'Copyright © 2019-2025 湖南创蓝信息科技有限公司版权所有',
        appNames:'创蓝教育实训云平台',
        imageUrlBack:"../assets/my/bjt.jpg",
        logo:'../assets/my/logo.png',
        loading: false,
        activeName: 'user',
        loginForm: {
          account: '',
          password: '',
          captcha:'',
          captchaUser:'',
          src: '',
        },
        phoneForm: {
          account: '15673144166',
        },
        fieldRules: {
          account: [
            { required: true, message: '请输入账号', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请输入密码', trigger: 'blur' }
          ] ,
          captcha: [
            { required: true, message: '请输入验证码', trigger: 'blur' }
          ]
        },
        checked: true
      }
    },
    methods: {
      findSettingData:function () {
        this.$api.settings.findData().then((res) => {
          if(res.data){
            for(let i=0;i<res.data.length;i++){
              if(res.data[i].settingValue){
                if(res.data[i].settingCode==="logoTitle"){
                  this.appNames=res.data[i].settingValue;
                }
                if(res.data[i].settingCode==="companyInfo"){
                  this.info=res.data[i].settingValue;
                }
                if(res.data[i].settingCode==="cbLogo"){
                  this.logo=baseUrl+'/uploads/settings/'+res.data[i].settingValue;
                }
                if(res.data[i].settingCode==="contactInfo"){
                  this.phoneCom=res.data[i].settingValue;
                }
                if(res.data[i].settingCode==="loginBgImg"){
                  this.imageUrlBack=baseUrl+'/uploads/settings/'+res.data[i].settingValue;
                  
                }
              }

            }
          }
          $(".center").css({"background":"url('"+this.imageUrlBack+"') no-repeat","background-size":"100% 100%"});
          $(".loginLogoStyle, .footer, .appNames").css({"display": "block"});

        });
      },
      login() {
        this.loading = true;
        this.$refs.loginForm.validate((valid) => {
          if (valid) {
            let userInfo = {username:this.loginForm.account, password:this.loginForm.password, captcha:this.loginForm.captcha}
            this.$api.login.login(userInfo).then((res) => {
              if(res.msg != null) {
                this.$message({
                  message: res.msg,
                  type: 'error'
                });
                this.refreshCaptcha()
              } else {
                Cookies.set('token', res.token) ;// 放置token到Cookie
                sessionStorage.setItem('user', userInfo.username);// 保存用户到本地会话
                sessionStorage.setItem('userRealname',  res.data.userRealname);
                sessionStorage.setItem('userimg', res.data.userimg);
                sessionStorage.setItem('userId', res.data.userId);
                console.log(res.data);
                this.$store.commit('menuRouteLoaded', false) // 要求重新加载导航菜单
                this.$router.push('/')  // 登录成功，跳转到主页
              }
            }).catch((res) => {
              this.$message({
                message: res.message,
                type: 'error'
              })
            });
          }
          this.loading = false
        });
      },
      refreshCaptcha: function(){
        this.loginForm.src = this.global.baseUrl + "/user/captcha.jpg?t=" + new Date().getTime();
      },
      reset() {
        this.$refs.loginForm.resetFields()
      },
      // 切换主题
      onThemeChange: function(themeColor) {
        this.$store.commit('setThemeColor', themeColor)
      }
    },
    mounted() {
      this.$nextTick( () =>{
        this.$refs.account.focus()
      })
      this.refreshCaptcha()
      this.findSettingData()
    },
    computed:{
      ...mapState({
        themeColor: state=>state.app.themeColor
      })
    }
  }
</script>

<style lang="scss" scoped>
  .center{
    width: 100%;
    height: calc(100vh - 150px);
    background-size: 100% 100%;
  }
  .xixi{
    padding-left: 60%;
    position: fixed;
    top: 0;
    bottom: 0;
    margin: auto;
  }
  .footer {
    font-size: 14px;
    text-align: center;
    height: 75px;
  }
  .footer a:hover {
    color: royalblue;
    cursor: pointer;
  }
  .footer p span{
    margin: 0 10px;
  }
  .login-container {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    /* margin: 100px auto;*/
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 15px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      /*margin: 0 40px 25px 40px;*/
    }
  }
</style>



