<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  created() {
    var asizeStyle='';
    asizeStyle=localStorage.getItem("asizeStyle");
    localStorage.removeItem("asizeStyle");
    if(asizeStyle){
      $("#asizeStyle").attr("href",asizeStyle); //实现将主题保存在内存中刷新浏览器不改变
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #484a4d;
  /* background:  #b6bbbb21; */
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
}
</style>



